@layer theme, base, components, utilities;
@layer theme {
  :root, :host {
    --default-font-family: var(--font-geist-sans);
    --default-mono-font-family: var(--font-geist-mono);
  }
}
@layer base {
  *, ::after, ::before, ::backdrop, ::file-selector-button {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0 solid;
  }
  html, :host {
    line-height: 1.5;
    -webkit-text-size-adjust: 100%;
    tab-size: 4;
    font-family: var(--default-font-family, ui-sans-serif, system-ui, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji");
    font-feature-settings: var(--default-font-feature-settings, normal);
    font-variation-settings: var(--default-font-variation-settings, normal);
    -webkit-tap-highlight-color: transparent;
  }
  hr {
    height: 0;
    color: inherit;
    border-top-width: 1px;
  }
  abbr:where([title]) {
    -webkit-text-decoration: underline dotted;
    text-decoration: underline dotted;
  }
  h1, h2, h3, h4, h5, h6 {
    font-size: inherit;
    font-weight: inherit;
  }
  a {
    color: inherit;
    -webkit-text-decoration: inherit;
    text-decoration: inherit;
  }
  b, strong {
    font-weight: bolder;
  }
  code, kbd, samp, pre {
    font-family: var(--default-mono-font-family, ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace);
    font-feature-settings: var(--default-mono-font-feature-settings, normal);
    font-variation-settings: var(--default-mono-font-variation-settings, normal);
    font-size: 1em;
  }
  small {
    font-size: 80%;
  }
  sub, sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
  }
  sub {
    bottom: -0.25em;
  }
  sup {
    top: -0.5em;
  }
  table {
    text-indent: 0;
    border-color: inherit;
    border-collapse: collapse;
  }
  :-moz-focusring {
    outline: auto;
  }
  progress {
    vertical-align: baseline;
  }
  summary {
    display: list-item;
  }
  ol, ul, menu {
    list-style: none;
  }
  img, svg, video, canvas, audio, iframe, embed, object {
    display: block;
    vertical-align: middle;
  }
  img, video {
    max-width: 100%;
    height: auto;
  }
  button, input, select, optgroup, textarea, ::file-selector-button {
    font: inherit;
    font-feature-settings: inherit;
    font-variation-settings: inherit;
    letter-spacing: inherit;
    color: inherit;
    border-radius: 0;
    background-color: transparent;
    opacity: 1;
  }
  :where(select:is([multiple], [size])) optgroup {
    font-weight: bolder;
  }
  :where(select:is([multiple], [size])) optgroup option {
    padding-inline-start: 20px;
  }
  ::file-selector-button {
    margin-inline-end: 4px;
  }
  ::placeholder {
    opacity: 1;
  }
  @supports (not (-webkit-appearance: -apple-pay-button))  or (contain-intrinsic-size: 1px) {
    ::placeholder {
      color: currentcolor;
      @supports (color: color-mix(in lab, red, red)) {
        color: color-mix(in oklab, currentcolor 50%, transparent);
      }
    }
  }
  textarea {
    resize: vertical;
  }
  ::-webkit-search-decoration {
    -webkit-appearance: none;
  }
  ::-webkit-date-and-time-value {
    min-height: 1lh;
    text-align: inherit;
  }
  ::-webkit-datetime-edit {
    display: inline-flex;
  }
  ::-webkit-datetime-edit-fields-wrapper {
    padding: 0;
  }
  ::-webkit-datetime-edit, ::-webkit-datetime-edit-year-field, ::-webkit-datetime-edit-month-field, ::-webkit-datetime-edit-day-field, ::-webkit-datetime-edit-hour-field, ::-webkit-datetime-edit-minute-field, ::-webkit-datetime-edit-second-field, ::-webkit-datetime-edit-millisecond-field, ::-webkit-datetime-edit-meridiem-field {
    padding-block: 0;
  }
  ::-webkit-calendar-picker-indicator {
    line-height: 1;
  }
  :-moz-ui-invalid {
    box-shadow: none;
  }
  button, input:where([type="button"], [type="reset"], [type="submit"]), ::file-selector-button {
    appearance: button;
  }
  ::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
    height: auto;
  }
  [hidden]:where(:not([hidden="until-found"])) {
    display: none !important;
  }
}
@layer utilities {
  .absolute {
    position: absolute;
  }
  .relative {
    position: relative;
  }
  .antialiased {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}
:root {
  --background: #d3c3a9;
  --foreground: #534947;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  font-family: 'Walter Turncoat',sans-serif;
  font-weight: normal;
  background: #d3c3a9;
  background-image: url("/coffe.PNG");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center top 80%;
  background-color: #d3c3a9;
  background: url("/coffe.PNG") center/cover no-repeat fixed, #d3c3a9;
  color: #534947;
  font-family: 'Walter Turncoat', sans-serif;
  text-align: center;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #534947;
    --foreground: #d3c3a9;
  }
}
.titledayo {
  font-weight: bold;
  font-size: 2.5rem;
  color: #534947;
  text-shadow: 2px 0 #FDFFF4,
  -2px 0 #FDFFF4,
  0 2px #FDFFF4,
  0 -2px #FDFFF4,
  2px 2px #FDFFF4,
  -2px -2px #FDFFF4,
  -2px 2px #FDFFF4,
  2px -2px #FDFFF4;
}
textarea {
  width: 80%;
  height: 100px;
  margin: 10px 0;
  padding: 10px;
  background-color: #FDFFF4;
  border-radius: 10px;
  border: 0px solid;
}
button {
  background-color: #5D432C;
  color: #FDFFF4;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  margin: 10px;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background-color: #534947;
}
.track-item {
  margin: 15px auto;
  padding: 10px;
  width: 320px;
  background-color: #FDFFF4;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(255, 28, 28, 0.1);
}
.note {
  margin: 15px auto;
  width: 80%;
  background-color: #FDFFF4;
  padding: 20px;
  border: 1px solid #FDFFF4;
  border-radius: 10px;
  background-image: linear-gradient(#53494725 1px, transparent 1px);
  background-size: 100% 1em;
  line-height: 1em;
  background-position-y: 1.38em;
}
.note p {
  margin: 0;
  padding: 0;
}
.demo01__ribbon {
  display: inline-block;
  margin: 0 0 0 -5px;
  padding-top: 8px;
  width: 100px;
  height: 36px;
  position: absolute;
  top: 89px;
  left: 36;
  color: #FDFFF4;
  box-sizing: border-box;
  background-color: #B8766A;
}
.demo01__title {
  margin: 0;
  padding-left: 25px;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  font-size: 1.4rem;
}
.demo01__title:before {
  position: absolute;
  top: 100%;
  left: 0;
  border: none;
  border-right: solid 5px #B8766A;
  border-bottom: solid 5px transparent;
  content: '';
}
.demo01__title:after {
  position: absolute;
  width: 0;
  height: 0;
  top: 0;
  right: -10px;
  z-index: 1;
  border-style: solid;
  border-width: 18px 10px 18px 0;
  border-color: #B8766A transparent;
  content: '';
}
html {
  scroll-behavior: smooth;
}
.backbtn {
  background: #5D432C;
  color: #FDFFF4;
  border: none;
  border-radius: 20px;
  padding: 8px 16px;
  cursor: pointer;
  margin: 10px;
  height: 50px;
  position: fixed;
  right: 30px;
  bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.backbtn:hover {
  background: #534947;
}


